/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .mkdf-content,
    div.woocommerce {

        .mkdf-quantity-buttons {

            .mkdf-quantity-input,
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                font-size: 13px;
            }

            .mkdf-quantity-input {
                height: 38px;
                width: 38px;
                margin: 0 19px 0 0;
                line-height: 36px;
            }

            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                width: 19px;
                height: 19px;
                line-height: 17px;
            }
        }
    }
    
    .woocommerce-page .mkdf-single-product-content {
    
        .mkdf-quantity-buttons {
        
            .mkdf-quantity-input,
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                font-size: 13px;
            }
        
            .mkdf-quantity-input {
                height: 54px;
                width: 54px;
                margin: 0 30px 0 0;
                line-height: 52px;
            }
        
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                width: 30px;
                height: 27px;
                line-height: 25px;
            }
        }
    }
}   
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */