/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {
	
	.mkdf-woocommerce-page {
		
		.cart-collaterals {
			
			h2 {
				margin: 1px 0 26px;
			}
		}
	}
}

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
					}
				}
			}
		}
		
		.cart-collaterals {
			
			h2 {
				margin: 51px 0 26px;
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			thead {
				
				tr {
					
					th {
						font-size: 14px;
					}
				}
			}
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-remove {
							padding-left: 0;
							line-height: 1;
						}
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 100%;
						margin: 0 0 20px 0;
					}
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
				
				button.button {
					float: none;
					display: block;
					width: 100%;
					margin: 0;
				}
			}
		}
		
		.cart-collaterals {
			
			table {
				
				td {
					padding-left: 14px;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */